import React from 'react';
import "./prof.scss";
import axios from 'axios';
import { Icon, Radio } from 'semantic-ui-react'
import {
    Button, Checkbox, Form, Grid,
    Message,
    Segment,
    Label,
    Input,

    Header,
} from 'semantic-ui-react'
import Pikachu from './components/layout/pikachu';
import Logo from './components/layout/partials/Logo';
class Intrest extends React.Component {

    state = {
        intrest: '',
        datad: [],
    };

    handleChange = (e, { value }) => {
        this.setState({
            [e.target.id]: e.target.value
        })

        this.setState({ value })
    };

    componentDidMount() {

        axios("https://3choices.in/profile/profile/")
            .then(res => {

                this.setState({ datad: res.data.filter(({ parent_user }) => parent_user === localStorage.getItem("username")) });

            })
            .catch(err => {
                this.setState({ error: err });
            });
    }

    handleSubmit = (e) => {
        e.preventDefault();

        let form_data = new FormData();
        form_data.append('parent_user', localStorage.getItem("username"));
        form_data.append('intrest', this.state.value);

        let url = 'https://3choices.in/profile/profile/';
        axios.put(url + localStorage.getItem("df") + "/", form_data, {
            headers: {
                'content-type': 'multipart/form-data'
            }

        }).then(res => {
            window.location.assign(`/main/products/${localStorage.getItem("df")}`);

        })



    }


    render() {
        const { datad } = this.state;
        datad.map((c) => {
            localStorage.setItem("df", c.id);

        })


        return (
            <>
                <Logo />
                <br></br>




                <Grid
                    textAlign="center"
                    style={{ height: "auto", position: "relative", top: "4rem" }}
                    verticalAlign="middle"


                >
                    <Grid.Column style={{ maxWidth: 450, backgroundImage: "linear-gradient(158deg ,rgb(255 255 255 / 25%),rgb(255 256 256 / 0)),url('http://source.unsplash.com/wSBQFWF77lI/490x1170')", boxShadow: "2px 3px 14px -1px #42d0ff", borderRadius: "3%", backgroundPosition: "56% 81%" }}>
                        <Header as="h2" className="editDetails" style={{ color: "#0b75ba" }} textAlign="center">Edit Your Details</Header>
                        <React.Fragment>

                            <Segment stacked style={{
                                backgroundColor: "#151719"
                            }} >
                                <Form style={{minHeight: "46vh"}} onSubmit={this.handleSubmit}>
                                    <Form.Field>

                                        <Label style={{fontWeight: 400 ,color: "white", backgroundColor: "#18ba47" }} for="location"> Intrested in: </Label>
                                    </Form.Field>
                                    <div style={{marginLeft: "40%"}}>
                                    <Form.Field>
                                        <Radio

                                            name='radioGroup'
                                            value='Male'
                                            style={{ position: "relative", marginTop: "8px" }}

                                            checked={this.state.value === 'Male'}
                                            onChange={this.handleChange}
                                        />
                                        <Label style={{fontWeight: 400 ,color: "white", backgroundColor: "crimson" }} for="radioGroup">Male</Label>
                                        <span class="interestOptions">
                                        <Radio

                                            name='radioGroup'

                                            value='Female'
                                            style={{ position: "relative", marginTop: "8px" }}
                                            checked={this.state.value === 'Female'}
                                            onChange={this.handleChange}
                                        />
                                        <Label style={{fontWeight: 400 ,color: "white", backgroundColor: "crimson"}} for="radioGroup">Female</Label>
                                        </span>
                                    </Form.Field>
                                    <Form.Field>
                                        <Radio

                                            name='radioGroup'
                                            value='Not-Sure'
                                            style={{ position: "relative", marginTop: "8px" }}
                                            checked={this.state.value === 'Not-Sure'}
                                            onChange={this.handleChange}
                                        />
                                        <Label style={{fontWeight: 400 ,color: "white", backgroundColor: "crimson"}} for="radioGroup">Not-Sure</Label>
                                        <br />
                                    </Form.Field>
                                    </div>



                                    <Button color="violet" style={{marginTop: "2rem", float: "right" }}  type='submit'>Submit</Button>

                                </Form>
                            </Segment>



                        </React.Fragment>
                    </Grid.Column>
                </Grid>
                <br></br>
                <br></br>
                <br></br>
                <br></br>
                <br></br>
            </>
        )
    }
}













export default Intrest