import React from 'react';
import PersonIcon from '@material-ui/icons/Person';
import "./Header.css"
import IconButton from "@material-ui/core/IconButton"
import ChatIcon from '@material-ui/icons/Chat';
import { Container, Label, Button, Accordion, Icon, Segment, Dimmer, Loader, Image } from "semantic-ui-react"
import { Link } from "react-router-dom"
import axios from "axios"
import { withRouter } from "react-router-dom";
class Header extends React.Component {
  state = {
    data: [],
    loading: false,
  }
  componentDidMount() {
    this.setState({ loading: true });
    axios("https://3choices.in/profile/profile/")
      .then(res => {
        this.setState({ data: res.data.filter(({ parent_user }) => parent_user === localStorage.getItem("username")), loading: false });

      })
  }
  render() {
    const { data, loading } = this.state;
    console.log(data.length);

    return <>
        {this.state.loading == true ? <>
            {loading && <Segment style={{ width: "100%",marginTop: "2rem"}}>
                            <Dimmer style={{ background: "transparent"}} active inverted>
                                <Loader inverted>Loading</Loader>
                            </Dimmer>

                            {/* 
                            <Image src="/images/wireframe/short-paragraph.png" alt=" " />
                            */}
                          </Segment>}
          </> : <Container className="heade">
            {data.length == 0 ? <IconButton style={{ backgroundColor: "rgb(51 155 160)" }} key={data.id} onClick={() => this.props.history.push(`/prof`)}>
                <Icon name="user" style={{ margin: 0 }} fontSize="larger" className="header-icon" />
              </IconButton> : data.map((r) => (
                <IconButton
                  style={{ backgroundColor: "rgb(51 155 160)" }}
                  key={r.id}
                  onClick={() =>
                    this.props.history.push(`/main/products/${r.id}`)
                  }
                >
                  <Icon
                    name="user"
                    style={{ margin: 0 }}
                    fontSize="larger"
                    className="header-icon"
                  />
                </IconButton>
              ))}

            <h4 style={{ color: "#172e2f" }}>
              Hey {localStorage.getItem("username")}!
            </h4>
            <a href="/cxz">
              <IconButton style={{ backgroundColor: "rgb(51 155 160)" }}>
                <Icon name="paper plane" fontSize="larger" className="header-icon" />
              </IconButton>
            </a>
          </Container>}
      </>;
  }
}

export default withRouter(Header);