import React, { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Link, withRouter } from "react-router-dom";
import Logo from './partials/Logo';
import { Button } from "semantic-ui-react"
import { logout } from "../../store/actions/auth";
import { connect } from "react-redux";
const propTypes = {
    navPosition: PropTypes.string,
    hideNav: PropTypes.bool,
    hideSignin: PropTypes.bool,
    bottomOuterDivider: PropTypes.bool,
    bottomDivider: PropTypes.bool
}

const defaultProps = {
    navPosition: '',
    hideNav: false,
    hideSignin: false,
    bottomOuterDivider: false,
    bottomDivider: false
}

const Pikachu = ({
    className,
    navPosition,
    hideNav,
    hideSignin,
    bottomOuterDivider,
    bottomDivider,
    ...props
}) => {

    const [isActive, setIsactive] = useState(false);

    const nav = useRef(null);
    const hamburger = useRef(null);

    useEffect(() => {
        isActive && openMenu();
        document.addEventListener('keydown', keyPress);
        document.addEventListener('click', clickOutside);
        return () => {
            document.removeEventListener('keydown', keyPress);
            document.addEventListener('click', clickOutside);
            closeMenu();
        };
    });

    const openMenu = () => {
        document.body.classList.add('off-nav-is-active');
        nav.current.style.maxHeight = nav.current.scrollHeight + 'px';
        setIsactive(true);
    }

    const closeMenu = () => {
        document.body.classList.remove('off-nav-is-active');
        nav.current && (nav.current.style.maxHeight = null);
        setIsactive(false);
    }

    const keyPress = (e) => {
        isActive && e.keyCode === 27 && closeMenu();
    }

    const clickOutside = (e) => {
        if (!nav.current) return
        if (!isActive || nav.current.contains(e.target) || e.target === hamburger.current) return;
        closeMenu();
    }

    const classes = classNames(
        'site-header',
        bottomOuterDivider && 'has-bottom-divider',
        className
    );

    return <header {...props} className={classes}>
        <div className="navbar" style={{ backgroundImage: "linear-gradient(rgba(0, 0, 0, 0.6),rgba(0, 0, 0, 0.6)), url(https://images.unsplash.com/photo-1604700403855-dc64a1320324)", backgroundSize: "inherit", backgroundPosition: "left", margin: "8px" }}>
          <Logo />
          {!hideNav && <>
              <button ref={hamburger} className="header-nav-toggle" onClick={isActive ? closeMenu : openMenu}>
                <span className="screen-reader">Menu</span>
                <span className="hamburger">
                  <span className="hamburger-inner" />
                </span>
              </button>
              <nav ref={nav} className={classNames("header-nav", isActive && "is-active")}>
                <div className="header-nav-inner" style={{ fontWeight: 400 }}>
                  <ul className={classNames("list-reset text-xs", navPosition && `header-nav-${navPosition}`)}>
                    <li>
                      {props.authenticated & (localStorage.getItem("status") != null) ? <Link header class="navbar_main_item">
                          <h5 style={{ cursor: "pointer" }} onClick={() => props.logout()}>
                            Logout
                          </h5>
                        </Link> : <Link to="/login2" class="navbar_main_item">
                          <h5 onClick={closeMenu}>Login</h5>
                        </Link>}
                    </li>

                    <li>
                      <Link to="/" class="navbar_main_item">
                        <h5 onClick={closeMenu}> Home</h5>
                      </Link>
                    </li>
                  </ul>
                  {!hideSignin && <ul className="list-reset header-nav-right" />}
                </div>
              </nav>
            </>}
        </div>
      </header>;
}

Pikachu.propTypes = propTypes;
Pikachu.defaultProps = defaultProps;

const mapStateToProps = state => {
    return {
        authenticated: state.auth.token !== null
    };
};

const mapDispatchToProps = dispatch => {
    return {
        logout: () => dispatch(logout())
    };
};

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Pikachu)
);
